<template>
  <div class="main-wrapper">
    <div class="header-wrapper">PROFILES</div>
    <div class="body-wrapper">
      <!-- Body Header Part -->
      <div class="body-header">
        <div class="select-your-profile-text">SELECT YOUR PROFILE</div>
        <div class="create-a-profile-section">
          <div
            class="create-button"
            v-if="!createAProfileInputBox"
            @click="createAProfileInputBox = !createAProfileInputBox"
          >CREATE A PROFILE</div>
          <div class="create-profile-input" v-if="createAProfileInputBox">
            <input type="text" id="create-profile-inputbox" />
            <button id="create-profile" @click="createNewProfile">Create</button>
          </div>
        </div>
      </div>

      <!-- Body Contents Part -->
      <div class="body-contents">
        <div class="profiles-section">
          <draggable group="people" v-model="profiles" @start="drag=true" @end="drag=false">
            <div v-for="profile in profiles" :key="profile" :id="profile" class="profileRow">
              <div class="profile-text">{{profile}}</div>
              <div class="profile-controls">
                <span
                  class="profile-a"
                  :value="`${profile}-a`"
                  @click="switchProfile(`${profile}-a`)"
                >A</span>
                <span
                  class="profile-b"
                  :value="`${profile}-b`"
                  @click="switchProfile(`${profile}-b`)"
                >B</span>
                <span class="profile-delete" :id="`${profile}`" @click="deleteProfile"></span>
              </div>
            </div>
          </draggable>
        </div>
      </div>
    </div>
    <SliderPanel />
    <div class="footer-wrapper"></div>
  </div>
</template>

<script>
import draggable from "vuedraggable";
import SliderPanel from "./SliderPanel.vue";

export default {
  name: "ProfilePanel",
  components: {
    draggable,
    SliderPanel,
  },
  data() {
    return {
      createAProfileInputBox: false,
      profiles: [
        "SuperBass",
        "Powerful",
        "Concert Hall",
        "Far Away",
        "Acoustic",
        "Pop",
      ],
    };
  },
  methods: {
    switchProfile: function (profile_value) {
      let profileName = profile_value.split("-")[0];
      let selectedVal = profile_value.split("-")[1];
      let profileNode = document.getElementById(profileName);
      profileNode.classList.remove("selectedRowA", "selectedRowB");
      selectedVal === "a"
        ? profileNode.classList.add("selectedRowA")
        : profileNode.classList.add("selectedRowB");
    },

    duplicateCheck: function (input) {
      var lowerCasedProfileNames = this.profiles.toString().toLowerCase();
      if (lowerCasedProfileNames.indexOf(input.toLowerCase()) > 0) {
        return false;
      }
      return true;
    },

    createNewProfile: function () {
      let createProfileInputboxElem = document.getElementById(
        "create-profile-inputbox"
      );
      let profileValue = createProfileInputboxElem.value;
      if (
        profileValue &&
        profileValue !== "" &&
        this.duplicateCheck(profileValue)
      ) {
        this.profiles.push(profileValue);
      } else {
        alert("Denied: Empty, Duplicate OR Invalid Profile Name");
      }
      createProfileInputboxElem.value = "";
      this.createAProfileInputBox = !this.createAProfileInputBox;
    },
    deleteProfile: function (event) {
      if (
        event.target.parentNode.parentNode.className.includes("selectedRow")
      ) {
        alert("Selected Profile can't be deleted");
      } else {
        this.profiles.splice(this.profiles.indexOf(event.target.id), 1);
      }
    },
  },
};
</script>

