
<template>
  <div class="slider-wrapper">
    <div id="elapsed-time" ref="elapsed-time">00:00</div>
    <div id="progress-bar-cont">
      <progress id="progress-bar" ref="progress-bar" value="0" max="300"></progress>
      <!-- <span id="progress-bar-ball" ref="progress-bar-ball"></span> -->
    </div>
    <div id="total-time" ref="total-time">00:00</div>
  </div>
</template>

<script>
export default {
  name: "SliderPanel",
  data() {
    return {
      totalTimeInMinutes: 5,
      totalSecondsElapsed: 0,
      startMinutes: 0,
      startSeconds: 0,
    };
  },
  mounted() {
    this.timingInitialSetup();
    this.runSliderTimer();
  },

  methods: {
    timingInitialSetup: function () {
      if (this.totalTimeInMinutes > 0 && this.totalTimeInMinutes < 9) {
        this.$refs["total-time"].innerHTML = `0${this.totalTimeInMinutes}:00`;
      } else {
        this.$refs["total-time"].innerHTML = `${this.totalTimeInMinutes}:00`;
      }
    },
    runSliderTimer: function () {
      const self = this;

      this.setIntervalFunction = setInterval(function () {
        self.totalSecondsElapsed += 1;
        self.updateProgressBarUi(self.totalSecondsElapsed);
        if (self.totalSecondsElapsed > 0 && self.totalSecondsElapsed < 10) {
          self.$refs[
            "elapsed-time"
          ].innerHTML = `00:0${self.totalSecondsElapsed}`;
        } else if (
          self.totalSecondsElapsed > 9 &&
          self.totalSecondsElapsed <= 59
        ) {
          self.$refs[
            "elapsed-time"
          ].innerHTML = `00:${self.totalSecondsElapsed}`;
        } else if (self.totalSecondsElapsed > 59) {
          if (self.totalSecondsElapsed % 60 === 0) {
            self.startMinutes += 1;
            self.startSeconds = 0;
          }

          if (self.startSeconds >= 0 && self.startSeconds <= 9) {
            self.$refs[
              "elapsed-time"
            ].innerHTML = `0${self.startMinutes}:0${self.startSeconds}`;
            ``;
          } else {
            self.$refs[
              "elapsed-time"
            ].innerHTML = `0${self.startMinutes}:${self.startSeconds}`;
          }
          self.startSeconds += 1;
        }

        if (self.totalSecondsElapsed === self.totalTimeInMinutes * 60) {
          clearInterval(self.setIntervalFunction);
        }
      }, 1000);
    },
    updateProgressBarUi: function (totalSecondsElapsed) {
      this.$refs["progress-bar"]["value"] = totalSecondsElapsed;
      // this.$refs["progress-bar-ball"].style.left = `${totalSecondsElapsed}px`;
    },
  },
};
</script>

<style lang="scss" scoped>
.slider-wrapper {
  margin: 20px 100px;
  padding: 20px;
  border: 1px solid #cccccc;
  display: flex;
  text-align: center;

  #elapsed-time,
  #total-time {
    flex: 1;
  }
  #progress-bar-cont {
    flex: 8;
    position: relative;
    #progress-bar {
      width: 100%;
      height: 8px;
    }
    progress,
    progress::-webkit-progress-value {
      background-color: #ea8e03;
    }
    progress::-webkit-progress-bar {
      background-color: #cccccc;
    }
    #progress-bar-ball {
      position: absolute;
      width: 20px;
      height: 20px;
      background-color: #ea8e03;
      border-radius: 50%;
      left: 0px;
      top: -2px;
    }
  }
}
</style>